import { lazy, useState } from "react";
import { useSelector } from "react-redux"
import style from "./Stories.module.css";
import images from "../../../helpers/images";

const StoryModal = lazy(() => import("../../UI/Modals/StoryModal"));

export default function Stories() {
  const authUser = useSelector((state) => state.user)
  const [storyShow, setStoryShow] = useState({ open: false, profileThumb: "", story: "", username: ""});
  return (
    <>
      <section className={style.stories}>
        <div
          className={`${style.item} ${style.itemAdd}`}
          onClick={() => setStoryShow(!storyShow)}
        >
          <div className={style.inner}>
            <span className={style.thumb}>
              <img
                src={authUser.profilePhoto}
                alt={authUser.username}
                className={style.thumb}
              />
            </span>
          </div>
          <label>Your story</label>
        </div>
        {images.map((image, index) => (
        <div key={index} className={style.item} onClick={() => setStoryShow({show: true, story: image, username: "aliraufdinler"})}>
          <div className={style.inner}>
            <span className={style.thumb}>
              <img
                src={image}
                alt="thumb"
                className={style.thumb}
              />
            </span>
          </div>
          <label>aliraufdinler</label>
        </div>))}
      </section>
      {storyShow.show ? (
        <StoryModal
          username={storyShow.username}
          date="19m"
          profileThumb="https://igdownloader.com/file?"
          story={storyShow.story}
          setStoryShow={setStoryShow}
        />
      ) : (
        ""
      )}
    </>
  );
}
